import * as actionTypes from '../Actions/ActionTypes';
import isEmpty from 'lodash/isEmpty';

const defaultState = {
  isVoterAuth: false,
  users: {},
};

export default function setVoterAuthReducers(state = defaultState, action = {}) {
  switch (action.type) {
    case actionTypes.SET_AUTH_VOTER_DETAILS:
      debugger;
      return {
        isVoterAuth: !isEmpty(action.payload),
        users: action.payload,
      };

    default:
      return state;
  }
}