import React, { Component } from 'react';
import { Segment, Form, TextArea, Button, FormField } from 'semantic-ui-react';
import {
  DateInput,
  TimeInput,
  DateTimeInput,
  DatesRangeInput
} from 'semantic-ui-calendar-react';
import axios from 'axios';
import config from '../../../config';
import * as stepActions from '../../../Actions/AdminStepActions/StepActions';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

class ElectionCreaction extends Component {
  constructor(props) {
    super(props);
    this.state={
      title:'',
      description:'',
      SDate: '',
      STime: '',
      ETime: '',
      ErrorMsg:'',
      isLoading:false,
    };
  }
    
    handleChange = (event, {name, value}) => {
      if (this.state.hasOwnProperty(name)) {
        this.setState({ [name]: value });
      }
    }
    handleTextChange = (e)=>{
      this.setState({
        [e.target.name]:e.target.value,
      });
    }
    handleSubmit=(e)=>{
      e.preventDefault();
      if(this.state.STime !== '' && this.state.title !== '' && this.state.description !== '' && this.state.SDate !== '' && this.state.ETime !== ''){
        this.setState({
          isLoading:true
        });
        const { title , description , SDate , STime , ETime } = this.state;
        axios.post(`${config.localhttp}/api/GetElection/Details`,{title,description,SDate,STime,ETime,email:this.props.getEmail})
          .then((result)=>{
            console.log(result);
            debugger;
            this.props.updateStep3.updateStep3Client();
            this.setState({
              isLoading:false
            });
          }).catch((error)=>{
            console.log(error);
          });
      }else{
        this.setState({
          ErrorMsg:true
        });
      }
    }
    ErrorDisplay=(flag)=>{
      if(flag){
        return(
          <div>
            <h4 style={{ color: 'red' }}>Field Required</h4>
          </div>
        );
      }
    }
    render() {
      return (
        <Segment color='violet'>
          <h1>Election Creation</h1>
          <Form onSubmit={this.handleSubmit} >
            {this.ErrorDisplay(this.state.ErrorMsg)}
            <Form.Field>
              <label>Election Title</label>
              <input onChange={this.handleTextChange} placeholder='Enter-Title' name='title' value={this.state.title}  />
            </Form.Field>
            <Form.Field>
              <label>Description</label>
              <TextArea onChange={this.handleTextChange} placeholder='Enter-Description'  name='description' value={this.state.description} autoheight={true} placeholer='give description regarding your election' />
            </Form.Field>
            <FormField>
              <DateInput
                name="SDate"
                placeholder="Starting-Date"
                value={this.state.SDate}
                iconPosition="left"
                onChange={this.handleChange}
              />
            </FormField>
            <FormField>
              <TimeInput
                name="STime"
                placeholder="Starting-Time"
                value={this.state.STime}
                iconPosition="left"
                onChange={this.handleChange}
              />
            </FormField>
            <FormField>
              <TimeInput
                name="ETime"
                placeholder="Ending-Time"
                value={this.state.ETime}
                iconPosition="left"
                onChange={this.handleChange}
              />
            </FormField>
            <Form.Field>
              <Button loading={this.state.isLoading} type='submit' color='violet'>Proceed</Button>
            </Form.Field>
          </Form>
        </Segment>
      );
    }
}
function mapDispatchToProps(dispatch) {
  return{
    updateStep3:bindActionCreators(stepActions,dispatch),
  };
}
function mapStateToProps(state){
  return{
    getEmail: state.adminAuthReducer && state.adminAuthReducer.users.email,
  };
}
export default connect(mapStateToProps,mapDispatchToProps)(ElectionCreaction);