import React, { Component } from 'react';
import { Button, Image, List, Input, Form , Segment } from 'semantic-ui-react';
import Validator from 'validator';
import axios from 'axios';
import config from '../../../config';

import { connect } from 'react-redux';
import * as stepActions from '../../../Actions/AdminStepActions/StepActions';
import { bindActionCreators } from 'redux';

class Voters extends Component {

  state = {
    email: '',
    name:'',
    errorFlag: false,
    EmailVaidation: false,
    serverSideError:false,
    isLoading:false,
    serverErrors:'',
    successmsg:false,
  }
  proceedFurther = () =>{
    axios.post(`${config.localhttp}/api/Step1/Flag`,{email:this.props.getEmail})
      .then((success)=>{
        this.props.updateStep1.updateStep1Client();
      }).catch((error)=>{
        console.log(error);
      });
  }
  handleDelete = (email) => {
    console.log('Event Handler called', email);
    // const voters1 = [...this.state.voters];
    // const voters2 = voters1.filter((item) => {
    //     if (item.email !== email)
    //         return true;
    // })
    // console.log(voters2);
    const voters = this.state.voters.filter((item) => item.email !== email);
    this.setState({ voters });
  }

  handleAdd = (e) => {
    e.preventDefault();
    const { email, name } = this.state;
    if (email !== '' && name !== '') {
      this.setState({
        isLoading:true,
      });
      axios.post(`${config.localhttp}/api/Addvoter/Details`, { email: email , name:name })
        .then((result) => {
          console.log(result);
          if(result && result.data && result.data && result.data.error && result.data.error.errors && result.data.error.errors.email ){
            this.setState({
              serverErrors:result.data.error.errors.email.message,
              isLoading:false,
              serverSideError:true,
              errorFlag:false,
              successmsg:false,
            });
          }else{
            this.setState({
              successmsg:true,
              serverSideError:false,
              isLoading:false,
              serverError:false,
              errorFlag:false,
            });
          }
        }).catch((error) => {
          console.log(error);
        });
    } else {
      this.setState({
        errorFlag: true
      });
    }
  }
  onTextChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  displayError = (flag) => {
    if (flag) {
      return (
        <div>
          <h4 style={{ color: 'red' }}>Field Required</h4>
        </div>
      );
    }
  }
  displayErrorEmail = (flag) => {
    if (flag) {
      return (
        <div>
          <h4>email must be unique</h4>
        </div>
      );
    }
  }
 displayServerErrors = (flag,msg,color) =>{
   if(flag){
     return(
       <div>
         <h4 style={{ color: color }}>{msg}</h4>
       </div>
     );
   }
 }
 render() {
   const { errorFlag, voters } = this.state;
   return (
     <Segment color='blue' >
       {this.displayError(errorFlag)}
       {this.displayErrorEmail(this.state.EmailVaidation)}
       {this.displayServerErrors(this.state.serverSideError,this.state.serverErrors,'red')}
       {this.displayServerErrors(this.state.successmsg,'Successfully Saved and Email Send to Vote along with creadential','green')}
       <Form onSubmit={this.handleAdd} >
       <Form.Field>
            <label>Email</label>
             <Input name='email' placeholder='enter-email' onChange={this.onTextChange} value={this.state.email} />
          </Form.Field>
          <Form.Field>
            <label>Name</label>
             <Input name='name' placeholder='enter-name' onChange={this.onTextChange} value={this.state.name} />
          </Form.Field>   
          <Form.Field>
             <Button loading={this.state.isLoading} type='submit' >Add voter</Button>
          </Form.Field>   
       </Form>
       <div style={{textAlign:'right'}} >
        <Button onClick={this.proceedFurther} labelPosition='right' color='green' icon='right chevron' content='Proceed' />
        </div>
     </Segment>
   );
 }
}
function mapDispatchToProps(dispatch) {
  return{
    updateStep1:bindActionCreators(stepActions,dispatch),
  };
}
function mapStateToProps(state) {
  return {
    getEmail: state.adminAuthReducer && state.adminAuthReducer.users.email,
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Voters);