import * as actionTypes from '../Actions/ActionTypes';

const defaultState = {
  isLoading: true,
};

export default function setInitLoaderReducer(state = defaultState, action = {}) {
  switch (action.type) {
    case actionTypes.SET_INIT_LOADER:
      debugger;
      return {
        isLoading: action.payload,
      };  
    
    default:
      return state;
  }
}