import * as actionTypes from '../ActionTypes';
import config from '../../config';
import axios from 'axios';

export function setElectionDetails(data) {
  return {
    type: actionTypes.SET_ELECTION_DETAILS,
    payload: data,
  };
}
export function getElectionDetailsIntoRedux(url) {
  debugger;
  return (dispatch) => {
    debugger;
    return axios.get(`${config.localhttp}${url}`).then((result) => {
      debugger;
      dispatch(setElectionDetails(result.data.data[0]));
      return result.data.data;
    }).catch((error) => {
      console.log(error);
    });
  };
}