import * as actionTypes from '../Actions/ActionTypes';

const defaultState = {
  StepData: null,
};

export default function setAdminStepData(state = defaultState, action = {}) {
  switch (action.type) {
    case actionTypes.SET_STEPS_INFO:
      return {
        StepData: action.payload,
      };
    case actionTypes.UPDATE_STEP_1_CLIENT:
      debugger;
      return {
        ...state,
        StepData: { ...state.StepData,
          step1: true,
        }
      };
    case actionTypes.UPDATE_STEP_2_CLIENT:
      debugger;
      return {
        ...state,
        StepData: { ...state.StepData,
          step2: true,
        }
      };
    case actionTypes.UPDATE_STEP_3_CLIENT:
      debugger;
      return {
        ...state,
        StepData: { ...state.StepData,
          step3: true,
        }
      };
    case actionTypes.UPDATE_STEP_4_CLIENT:
      debugger;
      return {
        ...state,
        StepData: { ...state.StepData,
          step4: true,
        }
      };
    case actionTypes.RESET_ALL_STEPS_CLIENT:
      debugger;
      return {
        ...state,
        StepData: { ...state.StepData,
          step1: false,
          step2: false,
          step3: false,
          step4: false,
        }
      };
    default:
      return state;
  }
}