import * as actionTypes from '../ActionTypes';
import setAuthorizationToken from '../../webToken/setAuthorizationToken';
import axios from 'axios';
import config from '../../config';
import jwt from 'jsonwebtoken';
export function setAdminLoginUser(data) {
  return {
    type: actionTypes.SET_ADMIN_USER_LOGIN,
    payload: data,
  };
};
export function logout() {
  debugger;
  return (dispatch) => {
    localStorage.removeItem('jwtToken');
    setAuthorizationToken(false);
    dispatch(setAdminLoginUser({}));
  };
}
export function Login(data) {
  debugger;
  return (dispatch) => {
    return axios.post(`${config.localhttp}/api/AdminAuth`, data).then((res) => {
      console.log("response: " + res)
      
      const token = res.data.token;
      console.log('token data: ' + res.data);
      console.log(token);
      localStorage.setItem('jwtToken', token);
      setAuthorizationToken(token);
      console.log(jwt.decode(token));
      dispatch(setAdminLoginUser(jwt.decode(token)));
      return res;
    });
  };
}