import * as actionTypes from '../Actions/ActionTypes';
import isEmpty from 'lodash/isEmpty';

const defaultState = {
  CandidateDetails: null,
};

export default function setCandidateDetails(state = defaultState, action = {}) {
  switch (action.type) {
    case actionTypes.SET_CANDIDATE_DETAILS:
      debugger;
      return {
        CandidateDetails:action.payload,
      };

    default:
      return state;
  }
}