import React, { Component } from 'react';
import { Icon, Grid, Step, Dimmer, Loader } from 'semantic-ui-react';
import ElectionCreation from '../Container/dashboardcomponents/ElectionCreaction';
import Voters from './dashboardcomponents/Voters';
import Candidates from './dashboardcomponents/Candidates';
import axios from 'axios';

import { Redirect } from 'react-router-dom';
import ElectionCreaction from '../../components/Container/dashboardcomponents/ElectionCreaction.jsx';
import CompletingInfo from '../../components/Container/dashboardcomponents/CompletionInfo.jsx';
import config from '../../config';
import * as AdminLoginActions from '../../Actions/AuthAdminActions/AdminActions';
import * as sharedActions from '../../Actions/SharedActions/sharedActions';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as electionActions from '../../Actions/SetElectionDetailsActions/ElectionDetailsAction';
import Results from '../../components/Container/dashboardcomponents/Results.jsx';
import * as adminStepActions from '../../Actions/AdminStepActions/StepActions';
import AdminSubRoutes from '../../routes/AdminSubRoutes.jsx';

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state={
      setAdminStepData:null,
      setElectionDetails:null,
    };
  }
  
  static getDerivedStateFromProps(nextProps, prevState) {
    debugger;
    if(prevState.setAdminStepData !== nextProps.getAdminStepData){
      debugger;
      return{
        setAdminStepData:nextProps.getAdminStepData.StepData,
        setElectionDetails:nextProps.getElectionDetails,
      };
    }
  }
  componentWillUnmount() {
    this.props.setLoaderState.setInitLoader(true);   
  }

  componentDidMount() {
    Promise.all([
      this.props.getAdminStepFromServer.getStepsDataIntoRedux('/get/AdminStep/data',this.props.email),
      this.props.getElectionDetailFromServer.getElectionDetailsIntoRedux('/api/getElection/Details'),
    ])
      .then((result)=>{
    
        console.log(result[0]);
        debugger;
        this.props.setLoaderState.setInitLoader(false);
      }).catch((err)=>{
        console.log(err);
      });
  }
    
  render() {
    const { setAdminStepData } = this.state;
    const {  setElectionDetails } = this.state;
    debugger;
    const MainComp = (
      <React.Fragment>
        <Grid columns='equal'>
          <Grid.Column width='4'>
            <Step.Group fluid vertical>
              <Step active={setAdminStepData && !setAdminStepData.step1} completed={setAdminStepData && setAdminStepData.step1}>
                <Icon name='box' />
                <Step.Content>
                  <Step.Title>Voters</Step.Title>
                  <Step.Description>Set and Manage Voters</Step.Description>
                </Step.Content>                 
              </Step>

              <Step active={setAdminStepData && !setAdminStepData.step2} completed={setAdminStepData && setAdminStepData.step2}  >
                <Icon name='users' />
                <Step.Content>
                  <Step.Title>Candidates</Step.Title>
                  <Step.Description>Set Candidates information</Step.Description>
                </Step.Content>
              </Step>
              <Step active={setAdminStepData && !setAdminStepData.step3} completed={setAdminStepData && setAdminStepData.step3}    >
                <Icon name='address card' />
                
                <Step.Content>
                  <Step.Title>Election Creation</Step.Title>
                  <Step.Description>Set Election Information</Step.Description>
                </Step.Content>
              </Step>

              <Step active={setAdminStepData && !setAdminStepData.step4} completed={setAdminStepData && setAdminStepData.step4}>
                <Icon name='handshake' />
                <Step.Content>
                  <Step.Title>Start Voting</Step.Title>
                  <Step.Description>review and start voting procedure</Step.Description>
                </Step.Content>
              </Step>
            </Step.Group>
          </Grid.Column>

          <Grid.Column >
            {(setAdminStepData && !setAdminStepData.step1) ? 
              <Voters  /> : 
              ((setAdminStepData && !setAdminStepData.step2)) ? 
                <Candidates /> : ((setAdminStepData && !setAdminStepData.step3)) ?
                  <ElectionCreaction /> : ((setAdminStepData && !setAdminStepData.step4)) ?
                    <CompletingInfo ElectionDetails={this.state.setElectionDetails}  /> : <Results />
            }
          </Grid.Column>

        </Grid>

      </React.Fragment>
    );
    const Loader1 = (
      <Dimmer active>
        <Loader />
      </Dimmer>
    );
    return (
      <div>
        {this.props.getInitLoaderState ? Loader1 : MainComp }
      </div>
    );
  }
}
function mapDispatchToProps(dispatch) {
  return {
    setLoaderState: bindActionCreators(sharedActions, dispatch),
    getAdminStepFromServer:bindActionCreators(adminStepActions,dispatch),
    getElectionDetailFromServer:bindActionCreators(electionActions,dispatch)
  };
}
function mapStateToProps(state) {
  debugger;
  return {
    getElectionDetails:state.electionDetails.ElectionDetails,
    getInitLoaderState: state.sharedReducer.isLoading,
    getAdminStepData:state.adminStepData,
    email: state.adminAuthReducer && state.adminAuthReducer.users.email,
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);