import React, { Component } from 'react';
import { Input, Button } from 'semantic-ui-react';
import { Grid, Segment, Form } from 'semantic-ui-react';
import * as voterActions from '../../Actions/VotersActions/voterActions';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as AdminLoginActions from '../../Actions/AuthAdminActions/AdminActions';

class Login extends Component {
  _isMounted = false;
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      isLoading: false,
      isMount: false,
    };
  }
  componentWillUnmount() {
    this.setState({
      isMount: false,
    });
  }
  componentDidMount() {
    this.setState({
      isMount: true,
    });
  }

  onTextChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  onHandleSubmit = (e) => {
    e.preventDefault();
    if (this.state.password !== '' && this.state.email !== '') {
      this.setState({
        isLoading: true,
      });
      Promise.all([this.props.loginActions.Login({ email: this.state.email, password: this.state.password })])
        .then((success) => {
          this.setState({
            isLoading: false,
          });
          this.props.history.push('/voterDash');

        }).catch((error) => {
          this.setState({
            isLoading: false,
          });
        });
    } else {
      this.setState({
        error: true
      });
    }
  }
  displayError = (flag, color, msg) => {
    if (flag) {
      return (
        <div style={{ color: color }} >
          {msg}
        </div>
      );
    }
  }

  render() {

    return (
      <div>
        <Grid columns="equal">
          <Grid.Column />
          <Grid.Column width={8}>
            <Segment>
              <h1>Login</h1>
              <Segment>
                {this.displayError(this.state.error,'red','feild required')}
                <Form onSubmit={this.onHandleSubmit} >
                  <Form.Field>
                    <label>Enter Email</label>
                    <Input onChange={this.onTextChange} name='email' value={this.state.email} placeholder="type email" />
                  </Form.Field>
                  <Form.Field>
                    <label>Enter Password</label>
                    <Input type="password" onChange={this.onTextChange} name='password' value={this.state.password} placeholder="type password" />
                  </Form.Field>
                  <Form.Field>
                    <label />
                  </Form.Field>
                  <Button loading={this.state.isLoading}  fluid type="submit" color="violet">
                    Login
              </Button>
                </Form>
              </Segment>
            </Segment>
          </Grid.Column>
          <Grid.Column />
        </Grid>
      </div>
    );
  }
}
function mapDispatchToProps(dispatch) {
  return {
    loginActions: bindActionCreators(voterActions, dispatch)
  };
}
export default connect(null, mapDispatchToProps)(Login);