import * as actionTypes from '../ActionTypes';
import config from '../../config';
import axios from 'axios';
export function setStepsInformation(data) {
  debugger;
  return {
    type: actionTypes.SET_STEPS_INFO,
    payload: data,
  };
}
export function resetAllSteps() {
  debugger;
  return {
    type: actionTypes.RESET_ALL_STEPS_CLIENT,
  };
}
export function updateStep1Client() {
  return{
    type:actionTypes.UPDATE_STEP_1_CLIENT,  
  };
}
export function updateStep3Client() {
  return{
    type:actionTypes.UPDATE_STEP_3_CLIENT,  
  };
}
export function updateStep2Client() {
  return{
    type:actionTypes.UPDATE_STEP_2_CLIENT,  
  };
}
export function updateStep4Client() {
  return{
    type:actionTypes.UPDATE_STEP_4_CLIENT,  
  };
}
export function getStepsDataIntoRedux(url, email) {
  debugger;
  return (dispatch) => {
    debugger;
    return axios.post(`${config.localhttp}${url}`, {
      email: email
    }).then((result) => {
      debugger;
      dispatch(setStepsInformation(result.data.data));
      return result.data.data;
    }).catch((error) => {
      console.log(error);
    });
  };
}