import * as actionTypes from '../ActionTypes';
import setAuthorizationToken from '../../webToken/setAuthorizationToken';
import axios from 'axios';
import config from '../../config';
import jwt from 'jsonwebtoken';
export function setVoterUser(data) {
  return {
    type: actionTypes.SET_AUTH_VOTER_DETAILS,
    payload: data,
  };
}
export function logout() {
  debugger;
  return (dispatch) => {
    localStorage.removeItem('jwtToken1');
    setAuthorizationToken(false);
    dispatch(setVoterUser({}));
  };
}
export function Login(data) {
  debugger;
  return (dispatch) => {
    return axios.post(`${config.localhttp}/api/voterAuth`, data).then((res) => {
      console.log("response: " + res);
      
      const token = res.data.token;
      console.log('token data: ' + res.data);
      console.log(token);
      localStorage.setItem('jwtToken1', token);
      setAuthorizationToken(token);
      console.log(jwt.decode(token));
      dispatch(setVoterUser(jwt.decode(token)));
      return res;
    });
  };
}