import * as actionTypes from '../Actions/ActionTypes';
import isEmpty from 'lodash/isEmpty';

const defaultState = {
  GetVoteCountDetails: null,
};

export default function setCandidateDetails(state = defaultState, action = {}) {
  switch (action.type) {
    case actionTypes.GET_VOTE_COUNT_DETAILS:
      debugger;
      return {
        GetVoteCountDetails:action.payload,
      };

    default:
      return state;
  }
}