import React, { Component } from 'react';
import { Menu, MenuItem } from 'semantic-ui-react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import config from '../../config';
import * as AdminLoginActions from '../../Actions/AuthAdminActions/AdminActions';
import * as voterLoginAction from '../../Actions/VotersActions/voterActions';

import logo from '../../images/votelogo.png';
class Head extends Component {
  state = {
  };

  handleItemClick = (e, { name }) => {
    this.setState(
      { activeItem: name }, () => {
        console.log(this.state.activeItem);
      });
  }
  handleLogout = () => {
    this.props.logout.logout();
  }
  handleLogout1=()=>{
    this.props.logout1.logout();
  };
  renderVoterLogoutRoute=(flag)=>{
    const { activeItem } = this.state;
    if (flag) {
      return (
        <Menu.Menu style={{position:'relative',top:5}} position='right'>
          <Link to='/' >
            <Menu.Item
              name='logout'
              active={activeItem === 'logout'}
              onClick={this.handleLogout1}
            />
          </Link>
        </Menu.Menu>
      );
    } 
  }
  renderLogoutRoute = (flag) => {
    const { activeItem } = this.state;
    if (flag) {
      return (
        <Menu.Menu style={{position:'relative',top:5}} position='right'>
          <Link to='/' >
            <Menu.Item
              name='logout'
              active={activeItem === 'logout'}
              onClick={this.handleLogout}
            />
          </Link>
        </Menu.Menu>

      );
    }
  }
  render() {
    console.log('head ', this.props);
    const { activeItem } = this.state;
    console.log('state activeItem: ', activeItem);
    return (
      <Menu style={{ margin: 0 }} className='ui violet menu' inverted stackable>
        <Menu.Item >
          <img style={{ borderRadius: 7 }} src={logo} />
        </Menu.Item>
        <Menu.Item>
          <h4>
            Translection
          </h4>
        </Menu.Item>
        <Menu.Menu style={{position:'relative',top:5}} position='right'>
          <Link to='/admin' >
            <Menu.Item
              name='Admin'
              active={activeItem === 'Admin'}
              // onClick={this.handleLogout}
            />
          </Link>
        </Menu.Menu>
        {this.renderLogoutRoute(this.props.auth)}
        {this.renderVoterLogoutRoute(this.props.auth1)}
      </Menu>
    );
  }
}
function mapStateToProps(state) {
  return {
    auth: state.adminAuthReducer.isAdminAuth,
    auth1:state.setVoterAuthDetails.isVoterAuth,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    logout: bindActionCreators(AdminLoginActions, dispatch),
    logout1:bindActionCreators(voterLoginAction,dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Head); 