import React, { Component } from 'react';
import { Input, Button } from 'semantic-ui-react';
import { Grid, Segment, Form ,Header } from 'semantic-ui-react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as AdminLoginActions from '../../Actions/AuthAdminActions/AdminActions';
class AdminLogin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      error: {},
      validateFeild:false,
      isLoading: false,
    };
  }
  onHandleSubmit = (e) => {
    e.preventDefault();
    if (this.state.email !== '' && this.state.password !== '') {
      this.setState({
        isLoading: true,
      });
      this.props.actionLogin.Login({ email: this.state.email, password: this.state.password })
        .then((success) => {
          console.log(success);
          debugger;
          this.props.history.push('/dashboard');

          this.setState({
            isLoading: false,
            error: {},
            email:'',
            password:'',
            validateFeild:false
          });
        })
        .catch((error) => {
          debugger;
          if(error && error.response && error.response.data && error.response.data.errors && error.response.data.errors.form){
            this.setState({
              isLoading:false,
              error:{
                unAuthError:true,
              }
            });
          }
          this.setState({
            isLoading:false
          })
        });
    } else {
      this.setState({
        error: {
          email: true,
          password: true,
          validateFeild:true,
        }
      });
    }
  };
  onHandleText = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  errorUnAuth=(flag)=>{
    if(flag){
      return(
        <div>
          <Header as='h4'>Invalid Credentials</Header>
        </div>
      );
    }
  }
  errorDisplay = (flag) => {
    if (flag) {
      return (
        <div>
          <p style={{ color: 'red' }}> Feild Required </p>
        </div>
      );
    }
  };
  render() {
    const { email, password } = this.state.error;
    return (
      <div>
        <Grid columns="equal">
          <Grid.Column />
          <Grid.Column width={8}>
            <Segment>
              <h1>Admin Login</h1>
              {this.errorDisplay(this.state.validateFeild)}
              {this.errorUnAuth(this.state.error.unAuthError)}
              <Segment>
                <Form onSubmit={this.onHandleSubmit}>
                  <Form.Field>
                    <label>Enter Email</label>
                    <Input
                      error={email}
                      onChange={this.onHandleText}
                      name="email"
                      value={this.state.email}
                      placeholder="type email"
                    />
                  </Form.Field>
                  <Form.Field>
                    <label>Enter Password</label>
                    <Input
                      error={password}
                      onChange={this.onHandleText}
                      name="password"
                      value={this.state.password}
                      type="password"
                      placeholder="type password"
                    />
                  </Form.Field>
                  <Form.Field>
                    <label />
                  </Form.Field>
                  <Button loading={this.state.isLoading} fluid type="submit" color="violet">
                    Sign in
                  </Button>
                </Form>
              </Segment>
            </Segment>
          </Grid.Column>
          <Grid.Column />
        </Grid>
      </div>
    );
  }
}
function mapDispatchToProps(dispatch) {
  return {
    actionLogin: bindActionCreators(AdminLoginActions, dispatch),
  };
}
export default connect(null, mapDispatchToProps)(AdminLogin); 