import React, { Component } from 'react';
import { Grid, Segment, Step, Dimmer, Button, Loader, Icon, Label, Menu, Table } from 'semantic-ui-react';
import { bindActionCreators } from 'redux';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import config from '../../../config';
import axios from 'axios';
import * as voterActions from '../../../Actions/VoterDetailsActions/VoterDetailsAction';
import VotingStatus from '../../Container/VoterDashboard/VotingStatus.jsx';
class VoterDashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      voterDetail: null,
      electionDetails: null,
      isLoading: true,
      candidateDetails: null,
      flag: false,
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    debugger;
    if ((prevState.voterDetail !== nextProps.getVoterDetails) && (nextProps && nextProps.getVoterDetails && !nextProps.getVoterDetails.isVotingStart)) {
      debugger;
      return {
        voterDetail: nextProps.getVoterDetails,
      };
    } else if ((prevState.getCandidateDetails !== nextProps.getCandidateDetails) && (nextProps && nextProps.getVoterDetails && nextProps.getVoterDetails.isVotingStart)) {
      return {
        voterDetail: nextProps.getVoterDetails,
        electionDetails: nextProps.getElectionDetails,
        candidateDetails: nextProps.getCandidateDetails
      };
    }
    return null;
  }

  componentDidMount() {
    Promise.all(
      [this.props.downloadVoterDetails.getVoterDetailsSetIntoRedux('/api/getVoterDetails/Voter', this.props.email),
      this.props.downloadElectionDetails.getElectionDetailsIntoRedux('/api/getElection/Details'),
      this.props.downloadCandidateDetails.getCandidateDetailsIntoRedux('/api/getCandidate/Data'),
      ]
    ).then((result) => {
      debugger;
      console.log(result);
      this.setState({
        isLoading: false,
      });
    }).catch((error) => {
      console.log(error);
    });
  }
  switchToVotingStatus = () => {
    // this.props.history.push({
    //   pathname: '/votingStatus',
    //   state: { voterDetail: this.state.voterDetail, electionDetails: this.state.electionDetails, email: this.props.email , candidateDetails:this.state.candidateDetails },
    // });
    axios.post(`${config.localhttp}/api/SetCandidate/Flag`, { email: this.props.email })
      .then((success) => {
        this.props.setFlagReadyToCastVoter.setReadyToCastVoteFlag(true);
      }).catch((error) => {
        console.log(error);
      });
  }
  renderVotingButton = (flag) => {
    if (flag) {
      return (
        <Button onClick={this.switchToVotingStatus} negative>Vote Now!</Button>
      );
    }
  }

  render() {
    const Loader1 = (
      <Dimmer active>
        <Loader />
      </Dimmer>
    );
    const subCom1 = (
      <Segment>
        <h1>Profile</h1>
        <Table celled>
          <Table.Body>
            <Table.Row>
              <Table.Cell>
                <Label color='blue' ribbon>{(this.state && this.state.voterDetail && this.state.voterDetail.ApplyAsCandidate) ? 'Candidate' : 'Voter'}</Label>
              </Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell>Name:</Table.Cell>
              <Table.Cell>{this.state && this.state.voterDetail && this.state.voterDetail.name}</Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell>Email:</Table.Cell>
              <Table.Cell>{this.state && this.state.voterDetail && this.state.voterDetail.email}</Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell>Vote Casted:</Table.Cell>
              <Table.Cell>{(this.state && this.state.voterDetail && this.state.voterDetail.isVoteCasted) ? 'Yes' : 'No'}</Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell>Voting Started:</Table.Cell>
              <Table.Cell>{(this.state && this.state.voterDetail && this.state.voterDetail.isVotingStart) ? 'Yes' : 'No'}</Table.Cell>
            </Table.Row>
          </Table.Body>
        </Table>
        {this.renderVotingButton(this.state && this.state.voterDetail && this.state.voterDetail.isVotingStart)}
        {/* {changeSeen(this.state.flag)} */}
      </Segment>
    );
    const { isLoading } = this.state;
    debugger;
    return (
      <div>
        <Grid columns='equal'>
          <Grid.Column>
            {/* <Segment>1</Segment> */}

          </Grid.Column>
          <Grid.Column width={8}>
            {isLoading ? Loader1 : (!this.state && this.state.voterDetail.readyToCastVote) ? subCom1 : <VotingStatus {...this.props} candidateDetails={this.state.candidateDetails} votingDetails={this.state.voterDetail} electionDetails={this.state.electionDetails} />}
          </Grid.Column>
          <Grid.Column>
            {/* <Segment>3</Segment> */}
          </Grid.Column>
        </Grid>
      </div>
    );
  }
}
function mapStateToProps(state) {
  debugger;
  return {
    getVoterDetails: state.setVoterDetails.VoterDetails,
    getElectionDetails: state.setElectionDetailonVotingSide && state.setElectionDetailonVotingSide.ElectionDetails,
    isElectionStartFlag: state.setElectionDetailonVotingSide && state.setElectionDetailonVotingSide.isVotingStart,
    email: state.setVoterAuthDetails && state.setVoterAuthDetails.users.email,
    getCandidateDetails: state.setCandidateDetails.CandidateDetails,
  };
}
function mapDispatchToProps(dispatch) {
  return {
    downloadVoterDetails: bindActionCreators(voterActions, dispatch),
    downloadElectionDetails: bindActionCreators(voterActions, dispatch),
    downloadCandidateDetails: bindActionCreators(voterActions, dispatch),
    setFlagReadyToCastVoter: bindActionCreators(voterActions, dispatch)
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(VoterDashboard);