import * as actionTypes from '../Actions/ActionTypes';
import isEmpty from 'lodash/isEmpty';

const defaultState = {
  VoterDetails: null,
};

export default function setVoterDetailsReducer(state = defaultState, action = {}) {
  switch (action.type) {
    case actionTypes.SET_VOTER_DETAILS:
      debugger;
      return {
        VoterDetails: action.payload,
      };

    case actionTypes.SET_FLAG_IS_VOTER_READY_TO_CAST_VOTE:
      debugger;
      return {
        ...state,
        VoterDetails: { ...state.VoterDetails,
          readyToCastVote: action.payload,
        }
      };
      case actionTypes.SET_FLAG_IS_VOTER_READY_TO_CAST_VOTE1:
      debugger;
      return {
        ...state,
        VoterDetails: { ...state.VoterDetails,
          readyToCastVote1: action.payload,
        }
      };
      case actionTypes.SET_FLAG_IS_VOTER_READY_TO_CAST_VOTE2:
      debugger;
      return {
        ...state,
        VoterDetails: { ...state.VoterDetails,
          readyToCastVote1: false,
        }
      };
     case actionTypes.SET_CAST_VOTE:
     debugger;
      return {
        ...state,
        VoterDetails: { ...state.VoterDetails,
          isVoteCasted: true,
        }
      };   
    default:
      return state;
  }
}