import React, { Component } from 'react';
import { Button, Label, Checkbox, Form, Input, Dropdown, Rail } from 'semantic-ui-react';
import { Grid, Segment } from 'semantic-ui-react';
import validator from './../../Validator/FormValidator.jsx';
const gender = [
  { key: 'genderM', value: 'male', text: 'Male' },
  { key: 'genderF', value: 'female', text: 'Female' }
];



class Registration extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstname: '',
      lastname: '',
      password: '',
      pasconfirm: '',
      email: '',
      gender: '',
      checked: false,
      errors: {},
      isLoading: false,
    };
  }

  toggle = () => this.setState({ checked: !this.state.checked })
  handleChange = (e, { value }) => {
    console.log(value);
    console.log(e);
    this.setState({ gender: value }
    );
  };
  isValid = () => {

    const { errors, isValid } = validator(this.state, 'regPageValidation');
    if (!isValid) {
      this.setState({ errors });
    }
    return isValid;
  }
  onTextChange = (e) => {
    console.log(e);
    console.log(e.target.value);
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  onhandleSubmit = (e) => {
    e.preventDefault();
    if (this.isValid()) {
      console.log(this.state);
      this.props.history.push('/login');
    } else {
      console.log(this.state.errors);
    }
  }

  render() {
    const { errors } = this.state;
    return (
      <React.Fragment>
        <Grid columns='equal'>
          <Grid.Column>
          </Grid.Column>
          <Grid.Column width={8}>
            <Segment color='violet'>
              <h1>Registeration</h1>
              {errors.pasconfirm ? (<div style={{color:'red'}} >password not matched</div>) : null }
              <Segment>
                <Form onSubmit={this.onhandleSubmit} >
                  <Form.Field required>
                    <label>First Name: </label>
                    <Input error={errors.firstname} name='firstname' onChange={this.onTextChange} focus placeholder='First Name' />
                  </Form.Field>
                  <Form.Field required>
                    <label>Last Name</label>
                    <Input error={errors.lastname} name='lastname' onChange={this.onTextChange} placeholder='Last Name' />
                  </Form.Field>
                  <Form.Field required>
                    <label>Email Address</label>
                    <Input name='email' error={errors.email} onChange={this.onTextChange} placeholder='Enter Email address' />
                  </Form.Field>

                  <Form.Field required>
                    <label>Password</label>
                    <Input name='password' error={errors.password} onChange={this.onTextChange} type='password' placeholder='Enter Password' />
                  </Form.Field>


                  <Form.Field required>
                    <label>Retype Password</label>
                    <Input name='pasconfirm' error={errors.pasconfirm} onChange={this.onTextChange} type='password' placeholder='Enter Password Again' />
                  </Form.Field>

                  <Form.Field required>
                    <label>Select gender</label>
                    <Dropdown name='gender' error={errors.gender} onChange={this.handleChange} placeholder='Select your gender' fluid selection options={gender} />
                  </Form.Field>
                  <Form.Field required>
                    <Checkbox name='checkbox' onChange={this.toggle} checked={this.state.checked} label='I agree to the Terms and Conditions' />
                    {(errors.checkbox) === true ? (<div style={{ color: 'red' }}> check required </div>) : null}
                  </Form.Field>
                  <Button fluid type='submit' color='violet'>Sign up</Button>
                </Form>
              </Segment>
            </Segment>
          </Grid.Column>
          <Grid.Column>
          </Grid.Column>
        </Grid>
      </React.Fragment>
    );
  }
}
export default Registration;