import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as voterActions from '../../../Actions/VoterDetailsActions/VoterDetailsAction';
import { Label, Card, Feed, Button } from 'semantic-ui-react';
import axios from 'axios';
import config from '../../../config';
import VotingView from '../../Container/VoterDashboard/VotingView.jsx';
import * as timeActions from '../../../Actions/TimeActions';
class VotingStatus extends Component {
  constructor(props) {
    super(props);
    this.state = {
      voterDetails: null,
      electionDetails: null,
      diff: null,
      Ediff: null,
      fixedDate: null,
      fixedDate2: null,
      STime: '',
      ETime: '',
      SDate: '',
      Ctime: '',
      SstateHours: null,
      EstateHours: null,
      RemainFlag: true,
      msgFlag: false,
      votingPage: true,
    };
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps && nextProps.electionDetails && nextProps.electionDetails.SDate) {
      const dateInRequiredFormate = timeActions.gettingDateIntoRequiredFormate(nextProps.electionDetails.SDate);
      const howmanytimetoStartvoting = timeActions.TotalVotingStartTimeInMiliseconds(dateInRequiredFormate, nextProps.electionDetails.STime);
      const howmanyTimetoFinishVoting = timeActions.TotalVotingEndTimeInMiliseconds(dateInRequiredFormate, nextProps.electionDetails.ETime);
      const getFixedDate = timeActions.getFixedDate(dateInRequiredFormate, nextProps.electionDetails.STime);
      const getFixedDate2 = timeActions.getFixedDate(dateInRequiredFormate, nextProps.electionDetails.ETime);
      const timeWith12hourFormateStime = timeActions.convert24To12(nextProps.electionDetails.STime);
      const timeWith12hourFormateEtime = timeActions.convert24To12(nextProps.electionDetails.ETime);
      const Statehours = Math.floor(howmanytimetoStartvoting / (60 * 60 * 1000));
      const Estatehours = Math.floor(howmanyTimetoFinishVoting / (60 * 60 * 1000));
      const getCurrentTime = timeActions.getCurrentTimeInto12hr();
      debugger;
      return {
        voterDetails: nextProps.votingDetails,
        electionDetails: nextProps.electionDetails,
        diff: howmanytimetoStartvoting,
        Ediff: howmanyTimetoFinishVoting,
        fixedDate: getFixedDate,
        fixedDate2: getFixedDate2,
        STime: timeWith12hourFormateStime,
        ETime: timeWith12hourFormateEtime,
        SDate: dateInRequiredFormate,
        CTime: getCurrentTime,
        SstateHours: Statehours,
        EstateHours: Estatehours,
      };
    }
    return null;
  }
  RediectToResult = () => {
    this.props.history.push('/Result')
  }
  geEmailFromServerLink = (email) => {
    debugger;
    console.log(email);
    axios.post(`${config.localhttp}/api/getVotinig/Link`, { email: this.props.location.state.email })
      .then((success) => {
        this.setState({
          msgFlag: true,
        });
        console.log(success);
      }).catch((error) => {
        console.log(error);
      });
  }
  tick() {
    if (!(this.state.SstateHours < 0)) {
      this.setState((prevState, props) => ({
        diff: prevState.fixedDate - (new Date()).getTime(),
      }));
    } else {
      this.setState((prevState, props) => ({
        Ediff: prevState.fixedDate2 - (new Date()).getTime(),
      }));
    }
  }
  displayRemainTimeInStartVoting = (flag, hours, hours2, mins2, mins, secs2, secs) => {
    if (!(hours < 0)) {
      debugger;
      return (
        <Feed.Event style={{ textAlign: 'center' }} >
          <Feed.Content>
            <div style={{ display: 'flex', flexDirection: 'row' }} >
              <div>
                <Feed.Date content='Remaining Time To Start Voting!' />
              </div>
              <div style={{ position: 'relative', bottom: 5 }} >
                <Label circular color={'yellow'} empty key={'yellow'} />
              </div>
            </div>
            <Feed.Summary>
              <h1>Remaining Time: {hours}:{mins}:{secs}</h1>
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>
      );
    }
    else if (!(hours2 < 0)) {
      return (
        <Feed.Event style={{ textAlign: 'center' }} >
          <Feed.Content>
            <div style={{ display: 'flex', flexDirection: 'row' }} >
              <div>
                <Feed.Date content='Voting Time Has Been Started!' />
              </div>
              <div style={{ position: 'relative', bottom: 5 }} >
                <Label circular color={'green'} empty key={'green'} />
              </div>
            </div>
            <Feed.Summary>
              <h1>Remaining Time: {hours2}:{mins2}:{secs2}</h1>
              <Button disabled={this.state && this.state.voterDetails && this.state.voterDetails.isVoteCasted} color='grey' onClick={() => this.setFlag()}  >Cast Vote!</Button>
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>
      );
    } else {
      return (
        <Feed.Event style={{ textAlign: 'center' }} >
          <Feed.Content>
            <div style={{ display: 'flex', flexDirection: 'row' }} >
              <div>
                <Feed.Date content='Voting Time Has Been Finished!' />
              </div>
              <div style={{ position: 'relative', bottom: 5 }} >
                <Label circular color={'grey'} empty key={'grey'} />
              </div>
            </div>
            <Feed.Summary>
              <Button color='green' onClick={this.RediectToResult} >Show Results</Button>
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>
      );
    }
  }
  componentDidMount() {
    this.interval = setInterval(
      () => {
        if ((!(this.state.SstateHours < 0)) || (!(this.state.EstateHours < 0))) {
          this.tick();
        } else {
          clearInterval(this.interval);
          axios.post(`${config.localhttp}/api/SetCandidate/Flag1False`, { email: this.state.voterDetails.email })
            .then((success) => {
              this.props.setFlagFalse.setFlagFalse();
              console.log(success);
            }).catch((error) => {
              console.log(error);
            });
        }
      }, 1000
    );
  }
  checkEmailInbox = (flag) => {
    if (flag) {
      return (
        <div>
          <h4>check email inbox !</h4>
        </div>
      );
    }
  }

  setFlag = () => {
    axios.post(`${config.localhttp}/api/SetCandidate/Flag1`, { email: this.state.voterDetails.email })
      .then((success) => {
        this.props.setFlagReadyToCastVoter1.setReadyToCastVoteFlag1(true);
        console.log(success);
      }).catch((error) => {
        console.log(error);
      });
  }
  displaySomething = (flag) => {
    if (flag) {
      debugger;
      return (
        <div>
          <VotingView CastVoteFunc={this.props.setFlagofCastVote} setFlagFlag={this.props.setFlagFalse} votingDetails={this.state.voterDetails} candidateDetails={this.props.candidateDetails} />
        </div>
      );
    }
  }
  render() {
    const { diff } = this.state;
    const { Ediff } = this.state;
    // const moment = new moment.duration(diff);
    const hours = Math.floor(diff / (60 * 60 * 1000));
    const mins = Math.floor((diff - (hours * 60 * 60 * 1000)) / (60 * 1000));
    const secs = Math.floor((diff - (hours * 60 * 60 * 1000) - (mins * 60 * 1000)) / 1000);

    const hours2 = Math.floor(Ediff / (60 * 60 * 1000));
    const mins2 = Math.floor((Ediff - (hours2 * 60 * 60 * 1000)) / (60 * 1000));
    const secs2 = Math.floor((Ediff - (hours2 * 60 * 60 * 1000) - (mins2 * 60 * 1000)) / 1000);

    console.log('next props state', this.state);
    debugger;
    console.log(this.props);
    console.log(this.state.electionDetails);
    debugger;

    return (
      <div>
        <Card centered={true} >
          <Card.Content  >
            {this.checkEmailInbox(this.state.msgFlag)}
            <Card.Header style={{ textAlign: 'center' }} >Voting Progress</Card.Header>
          </Card.Content>
          <Card.Content>
            <Feed>
              <Feed.Event>
                <Feed.Content>
                  <Feed.Summary>
                    Voting Start Date: <a>{this.state && this.state.SDate}</a>
                  </Feed.Summary>
                </Feed.Content>
              </Feed.Event>

              <Feed.Event>
                <Feed.Content>
                  <Feed.Summary>
                    Voting Start Time: <a>{this.state && this.state.STime}</a>
                  </Feed.Summary>
                </Feed.Content>
              </Feed.Event>

              <Feed.Event>
                <Feed.Content>
                  <Feed.Summary>
                    Voting End Time: <a>{this.state && this.state.ETime}</a>
                  </Feed.Summary>
                </Feed.Content>
              </Feed.Event>
              {this.displayRemainTimeInStartVoting(this.state && this.state.RemainFlag, hours, hours2, mins2, mins, secs2, secs)}
            </Feed>
          </Card.Content>
        </Card>
        {this.displaySomething(this.state && this.state.voterDetails && this.state.voterDetails.readyToCastVote1)}
      </div>
    );
  }
}
function mapDispatchToProps(dispatch) {
  return {
    setFlagReadyToCastVoter1: bindActionCreators(voterActions, dispatch),
    setFlagFalse: bindActionCreators(voterActions, dispatch),
    setFlagofCastVote: bindActionCreators(voterActions, dispatch)
  };
}
export default connect(null, mapDispatchToProps)(VotingStatus);