import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import configureStore from './Store/configureStore';
import { Provider } from 'react-redux';
import 'semantic-ui-css/semantic.min.css';
import * as serviceWorker from './serviceWorker';
import { BrowserRouter } from 'react-router-dom';
import setAuthorizationToken from './webToken/setAuthorizationToken';
import * as AdminActions from './Actions/AuthAdminActions/AdminActions';
import * as voterActions from './Actions/VotersActions/voterActions';
import jwt from 'jsonwebtoken';

// yarn add react-router-dom --save 

const store = configureStore();

if(localStorage.jwtToken){
  setAuthorizationToken(localStorage.jwtToken);
  store.dispatch(AdminActions.setAdminLoginUser(jwt.decode(localStorage.jwtToken)));
}
if(localStorage.jwtToken1){
  setAuthorizationToken(localStorage.jwtToken1);
  store.dispatch(voterActions.setVoterUser(jwt.decode(localStorage.jwtToken1)));
}


ReactDOM.render(
  <BrowserRouter>
    <Provider store={store} >
      <App />
    </Provider>
  </BrowserRouter>
  , document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
