import React, {
  Component
} from 'react';
import {
  connect
} from 'react-redux';

export default function (ComposeComponent) {
  class Authenticate extends Component {
    constructor(props) {
      super(props);
      if (this.props.auth && this.props.location.pathname === '/') {
        debugger;
        this.props.history.push('/dashboard');
      } else if (!this.props.auth && this.props.location.pathname === '/dashboard') {
        debugger;
        this.props.history.push('/');
      } else if (this.props.auth && this.props.location.pathname === '/Admin') {
        this.props.history.push('/dashboard');
      } else if (this.props.isVAuth && this.props.location.pathname === '/') {
        this.props.history.push('/voterDash');
      } else if (!this.props.isVAuth && this.props.location.pathname === '/voterDash') {
        this.props.history.push('/');
      }
    }
    render() {
      return ( < ComposeComponent { ...this.props
      }
      />
      );
    }
  }

  function mapStateToProps(state) {
    debugger;
    return {
      auth: state.adminAuthReducer.isAdminAuth,
      getAdminStepData: state.adminStepData,
      isVAuth: state.setVoterAuthDetails.isVoterAuth,
      getVoterDetails: state.setVoterDetails.VoterDetails,
    };
  }

  return connect(mapStateToProps, null)(Authenticate);
}