import React, { Component } from 'react';
import { Segment, Button, TextArea, Image, List, Input, Form } from 'semantic-ui-react';
import Validator from 'validator';
import * as stepActions from '../../../Actions/AdminStepActions/StepActions';
import { bindActionCreators } from 'redux';

import axios from 'axios';
import config from '../../../config';
import { connect } from 'react-redux';
class Candidates extends Component {

  state = {
    email: '',
    name:'',
    description:'',
    errorFlag: false,
    EmailVaidation: false,
    serverSideError:false,
    isLoading:false,
    serverErrors:'',
    successmsg:false,
  }

  handleDelete = (email) => {
    console.log('Event Handler called', email);
    // const voters1 = [...this.state.voters];
    // const voters2 = voters1.filter((item) => {
    //     if (item.email !== email)
    //         return true;
    // })
    // console.log(voters2);
    const voters = this.state.voters.filter((item) => item.email !== email);
    this.setState({ voters });
  }

  proceedFurther = () =>{
    axios.post(`${config.localhttp}/api/Step2/Flag`,{email:this.props.getEmail})
      .then((success)=>{
        this.props.updateStep2.updateStep2Client();
      }).catch((error)=>{
        console.log(error);
      });
  }

  handleAdd = (e) => {
    e.preventDefault();
    const { email, name } = this.state;
    if (email !== '' && name !== '') {
      axios.post(`${config.localhttp}/api/Step1/Flag`, { email: this.props.getEmail })
        .then((result) => {
          console.log(result);
        }).catch((error) => {
          console.log(error);
        });
    } else {
      this.setState({
        errorFlag: true
      });
    }
  }
  onTextChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  displayError = (flag) => {
    if (flag) {
      return (
        <div>
          <h4 style={{ color: 'red' }}>Field Required</h4>
        </div>
      );
    }
  }
  displayErrorEmail = (flag) => {
    if (flag) {
      return (
         <div>
          <h4>email must be unique</h4>
        </div>
      );
    }
  }
  handleSubmit=(e)=>{
    e.preventDefault();
    console.log(this.state);
    if(this.state.email !== '' && this.state.description !== ''){
      this.setState({
        isLoading:true,
      });
      axios.post(`${config.localhttp}/api/AddCandidate/Details`, { ademail:this.props.getEmail, email: this.state.email , description:this.state.description })
        .then((result) => {
          debugger;
          if(result && result.data && !result.data.status){
            debugger;
            this.setState({
              serverErrors:result.data.msg,
              isLoading:false,
              serverSideError:true,
              errorFlag:false,
              successmsg:false,
            });
          }else{
            debugger;
            this.setState({
              successmsg:true,
              email:'',
              description:'',
              serverSideError:false,
              isLoading:false,
              serverError:false,
              errorFlag:false,
            });
          }
        }).catch((error) => {
          console.log(error);
        });
      
    }else{
      debugger;
      this.setState({
        errorFlag:true,
      })
    }
  }
  displayError = (flag) => {
    if (flag) {
      return (
        <div>
          <h4 style={{ color: 'red' }}>Field Required</h4>
        </div>
      );
    }
  }
  handleTextChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  displayServerErrors = (flag,msg,color) =>{
    if(flag){
      return(
        <div>
          <h4 style={{ color: color }}>{msg}</h4>
        </div>
      );
    }
  }
  render() {
    const { errorFlag, voters } = this.state;
    return (
      <Segment color='violet'>

        <Form onSubmit={this.handleSubmit} >
          <h1>Candidates</h1>
          {this.displayError(this.state.errorFlag)}
          {this.displayServerErrors(this.state.serverSideError,this.state.serverErrors,'red')}
          {this.displayServerErrors(this.state.successmsg,'Candidate Successfully Added!','green')}
          <Form.Field>
            <label>Email</label>
            <input onChange={this.handleTextChange} placeholder='Enter-Email' name='email' type='email' value={this.state.email} />
          </Form.Field>
          <Form.Field>
            <label>Description</label>
            <TextArea onChange={this.handleTextChange} name='description' placeholder='Enter-Description'  value={this.state.description} autoheight={true} placeholer='give description regarding your election' />
          </Form.Field>
         <Button>Add Candidate</Button>
        </Form>
        <div style={{textAlign:'right'}} >
        <Button onClick={this.proceedFurther} labelPosition='right' color='green' icon='right chevron' content='Proceed' />
        </div>
      </Segment>
    );
  }
}
function mapDispatchToProps(dispatch) {
  return{
    updateStep2:bindActionCreators(stepActions,dispatch),
  };
}
function mapStateToProps(state) {
  return {
    getEmail: state.adminAuthReducer && state.adminAuthReducer.users.email,
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Candidates);