import { combineReducers } from 'redux';
import adminAuthReducer from '../SetAdminReducer';
import sharedReducer from '../SharedReducer';
import adminStepData from '../SetStepAdminDataRed';
import electionDetails from '../SetElectionDetails';
import setVoterAuthDetails from '../SetVoterAuthDetailsRed';
import setVoterDetails from '../SetVoterDetailsRed';
import setElectionDetailonVotingSide from '../SetElectionDetailVoterSideRed';
import setCandidateDetails from '../SetCandidateDetailsRed';
import setVoteCountDetails from '../GetVoteCountDetailRed';
const rootReducer = combineReducers({
  adminAuthReducer,sharedReducer , adminStepData , electionDetails ,setVoterAuthDetails , setVoterDetails ,setElectionDetailonVotingSide , setCandidateDetails ,setVoteCountDetails
});

export default rootReducer;