import * as actionTypes from '../Actions/ActionTypes';
import isEmpty from 'lodash/isEmpty';

const defaultState = {
  isVotingStart: false,
  ElectionDetails: null,
};

export default function setElectionDetailsToVoting(state = defaultState, action = {}) {
  switch (action.type) {
    case actionTypes.SET_ELECTION_DETAILS:
      debugger;
      return {
        ElectionDetails:action.payload,
        isVotingStart:!isEmpty(action.payload)
      };

    default:
      return state;
  }
}