import React, { Component } from 'react';
import { Button } from 'semantic-ui-react';
import { Card, Icon } from 'semantic-ui-react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import config from '../../../config';
import axios from 'axios';
import * as stepActions from '../../../Actions/AdminStepActions/StepActions';
import * as voterActions from '../../../Actions/VoterDetailsActions/VoterDetailsAction';

class Results extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      Result: null,
      getWinResult: null,
      displayFlag: false,
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.Result !== nextProps.getVoteCountDetails) {
      debugger;
      return {
        Result: nextProps.getVoteCountDetails
      };
    }
    return null;
  }
  componentDidMount() {
    this.props.downloadVoteCount.getVoteCountDetailsIntoRedux('/api/getVote/Count')
      .then((result) => {
        this.setState({
          isLoading: false,
          isDraw: false
        });
      });
  }
  getResult = () => {
    const getVoteCount = this.state.Result;
    var maxVote = Number.MIN_VALUE;
    getVoteCount.forEach(function (item) {
      maxVote = Math.max(maxVote, item.Vote_Count);
    });

    let getFilter = getVoteCount.filter((item, index) => {
      if (item.Vote_Count == maxVote) {
        return item;
      }
    });

    console.log(getFilter);
    if (getFilter.length !== 1) {
      this.setState({
        isDraw: true,
      })
      return;
    } else {
      this.setState({
        getWinResult: getFilter,
        displayFlag: true,
      })
    }

  }
  displayWinner = (flag) => {
    debugger;
    const { getWinResult } = this.state;
    if (flag) {
      return (
        <div style={{ textAlign: 'center' }} >
          <Card.Group centered={true} >
            {getWinResult.map((item, index) => (
              <Card color='green  ' key={index} >
                <Card.Content header='Winner' />
                <Card.Content description={`${item._id}`} />
                <Card.Content extra>
                  <Icon name='user' />
                  {`${item.Vote_Count}`}
                </Card.Content>
              </Card>
            ))}
          </Card.Group>
        </div>
      )
    }
  }

  procedureDone = () => {
    this.setState({
      isLoading: true,
    });
    axios.post(`${config.localhttp}/api/DoneConfimation`, { email: this.props.getEmail })
      .then((success) => {
        this.props.resestAllSteps.resetAllSteps();
        this.setState({
          isLoading: false,
        })
      }).catch((error) => {
        console.log(error);
      });
  }
  render() {
    const { Result } = this.state;
    const { displayFlag } = this.state;
    debugger;
    return (
      <div>
        <h1>Results</h1>
        {Result && Result.map((item, index) => (
          <Card key={index} >
            <Card.Content header='Candidate' />
            <Card.Content description={`${item._id}`} />
            <Card.Content extra>
              <Icon name='user' />
              {`${item.Vote_Count}`}
            </Card.Content>
          </Card>
        ))}
        <Button primary onClick={this.getResult} >Press for Results </Button>
        {this.displayWinner(this.state.displayFlag)}
        <Button loading={this.state.isLoading} onClick={this.procedureDone} color='olive' >Done</Button>
      </div>
    );

  }
}

function mapDispatchToProps(dispatch) {
  return {
    resestAllSteps: bindActionCreators(stepActions, dispatch),
    downloadVoteCount: bindActionCreators(voterActions, dispatch)
  };
}
function mapStateToProps(state) {
  return {
    getEmail: state.adminAuthReducer && state.adminAuthReducer.users.email,
    getVoteCountDetails: state.setVoteCountDetails.GetVoteCountDetails,
 
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Results);